<template>
	<div id="agreement-settle">
		<public-header title="服务协议/隐私政策"></public-header>
		<div class="content">
			<div class="richtext" v-html="agreement"></div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				agreement: '',
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					url: 'manage/shop/index/agreement',
					vue: that,
					success: function(result) {
						that.agreement = result.agreement;
					}
				});
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#agreement-settle .richtext{
		padding: 10px;
		line-height: 1.5;
		width: 100%;
	}
</style>


